function tologo(){
	uni.showModal({
	    title: '',
	    content: ',',
		confirmColor:'#1296DB',
	    success: function (res) {
	        if (res.confirm) {
				uni.redirectTo({
					url:'/pages/mine/login/login'
				})
				console.log('');
	        } else if (res.cancel) {
	            console.log('');
	        }
	    }
	});
	return
}
export default tologo